import React from 'react';
import {useSpring, animated} from 'react-spring'
import './Home.css'

import HomeSVG from '../svg/remotely.svg'
import NatureSVG from '../svg/nature.svg'
const Home = () => {

    const props = useSpring({opacity: 1, from: {opacity: 0}})
    return(
        <animated.div style={props} className = "home" id ="Home">
            <h1>Hello, I'm Elizabeth</h1>
            <div className = "homeSvg">
                <img src={HomeSVG} alt="home-image"/>
            </div>
            <p>Helping others along the way ☺️ Be Your Own Inspiration</p>   
        </animated.div>
  );
}

export default Home;