import React from 'react'
import './DrawerButton.css';
import MenuSharpIcon from '@material-ui/icons/MenuSharp';
import MenuOpenIcon from '@material-ui/icons/MenuOpen';

const DrawerButton = (props) => {
    let drawerButtonClasses = 'toolbar-button';
    if(props.clicked){
        drawerButtonClasses = 'toolbar-button open';
    }

    return(
        <div className = {drawerButtonClasses}>
            {props.clicked? <MenuOpenIcon style={{ fontSize: 45 }} /> : <MenuSharpIcon style={{ fontSize: 45 }}/> }  
        </div>
    )
}
export default DrawerButton;